<template>
  <div class="options-menu" ref="menu-el">
    <BaseButton @click="toggleMenu" slot="reference" ref="button-el">
      <template v-slot:icon>
        <ChecIcon :icon="isOpen ? 'up' : 'down'" />
      </template>
    </BaseButton>
    <BasePopover
      v-show="isOpen"
      @option-selected="handleSelectOption"
      ref="popper-el"
    >
      <!--
        @slot Provide BaseOption instances here
      -->
      <slot />
    </BasePopover>
  </div>
</template>
<script>
import { createPopper } from '@popperjs/core';
import BaseButton from './BaseButton.vue';
import BasePopover from './BasePopover.vue';
import ChecIcon from './ChecIcon.vue';

export default {
  name: 'ChecOptionsMenu',
  components: {
    BaseButton,
    BasePopover,
    ChecIcon,
  },
  props: {
    /**
     * Whether the menu should initially be open or not
     */
    open: {
      type: Boolean,
      default: false,
    },
    /**
     *  Describes the preferred placement of the options menu.
     */
    menuPlacement: {
      type: String,
      default: 'bottom',
      validate(placement) {
        return [
          'auto',
          'auto-start',
          'auto-end',
          'top',
          'top-start',
          'top-end',
          'bottom',
          'bottom-start',
          'bottom-end',
          'right',
          'right-start',
          'right-end',
          'left',
          'left-start',
          'left-end'].includes(placement);
      },
    },
  },
  data() {
    return {
      isOpen: this.open,
    };
  },
  watch: {
    isOpen(newVal, oldVal) {
      if (newVal !== oldVal) {
        if (newVal) {
          this.show();
        } else {
          this.hide();
        }
      }
    },
  },
  created() {
    // add event listener to listen to outside click events
    window.addEventListener('click', this.onOutsideClick);
  },
  mounted() {
    this.$nextTick(() => {
      this.createPopper();
    });
  },
  beforeDestroy() {
    // remove event listeners
    window.removeEventListener('click', this.onOutsideClick);
  },
  methods: {
    /**
     * Show the popper.js
     */
    show() {
      this.$refs['button-el'].$el.setAttribute('data-show', '');
      this.createPopper();
    },
    /**
     * Hide the popper
     */
    hide() {
      this.$refs['popper-el'].$el.removeAttribute('data-show');
      this.destroyPopper();
    },
    /**
     * Create the popper.js instance
     */
    createPopper() {
      this.$popper = createPopper(this.$refs['button-el'].$el, this.$refs['popper-el'].$el, {
        placement: this.menuPlacement,
        modifiers: [
          {
            name: 'flip',
            options: {
              fallbackPlacements: ['top', 'bottom'],
            },
          },
          {
            name: 'preventOverflow',
            options: {
              rootBoundary: 'document',
            },
          },
        ],
      });
    },
    /**
     * Destroys the popper.js instance
     */
    destroyPopper() {
      if (this.$popper) {
        this.$popper.destroy();
        this.$popper = null;
      }
    },
    /**
     * Toggles the menu between open and closed
     */
    toggleMenu() {
      this.isOpen = !this.isOpen;
    },
    /**
     * A handler bound on the window while the component is mounted that closes the menu when clicked away
     *
     * @param {Event} event
     */
    onOutsideClick(event) {
      if (!this.$refs['menu-el'].contains(event.target) && this.isOpen) {
        this.isOpen = false;
      }
    },
    /**
     * When an option is selected, close the menu
     */
    handleSelectOption() {
      this.isOpen = false;
    },
  },
};
</script>
<style scoped lang="scss">
.options-menu {
  @apply static;
  .button {
    @apply shadow-none bg-transparent;
  }

  .popover {
    min-width: 10rem;
  }
}
</style>
